@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body {
  font-family: 'Roboto', sans-serif;
}
.app {
  display: flex;
  padding-top: 5rem;
  justify-content: center;
}

.wrap {
  width: 500px;
}
$height: 46px;
.select {
  &.open {
    .select__dropdown {
      display: block;
    }
    .select__input {
      border-bottom: none;
    }
  }
  position: relative;
  width: 100%;
  &__input {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    height: $height;
    cursor: pointer;
  }
  &__dropdown {
    display: none;
    position: absolute;
    border: 1px solid #ccc;
    border-radius: 5px;
    top: $height;
    left: 0;
    right: 0;
    max-height: 200px;
    overflow-y: auto;
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
    
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    padding: 1rem;
    border-bottom: 1px solid #ccc;
    transition: .15s background-color ease-in;
    &.selected {
      background-color: #008697;
      color: #fff;
    }
    &:hover {
      background-color: #eee;
      cursor: pointer;
    }

  }
}